
.movie-card {
    display: grid;
    justify-content: center;
    position: relative;
    cursor: pointer;
    
}
/* 
.movie-card:hover .info-container{
    opacity: 1;
} */
.movie-card img{
    border-radius: 5px;
}
.info-container {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0%;
    background-color: #00000080;
    width: 100%;
    transition: 200ms ease;
    opacity: 1;
}
.vote {
    display: block;
}