*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box  ;
}
.App {
  text-align: center;
  background-color: #000000;
  display: grid;
}
.banner {
  height: 70vh;
}

h1.api-connection-err {
  width: 100vw;
  height: 100vh;
  text-align: center;
  display: grid;
  align-content: center;
  justify-items: center;
  align-items: center;
  justify-content: center;
}
.banner h1 , .App-header p{
  color: #ffffff;
}
.banner-container {
  position: relative;
  width: 100vw;
  overflow: hidden;
  color: white;
}
.banner-info {
  position: absolute;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100vw;
  height: 70vh;
  padding-left:3vw ;
  padding-right: 3vw;
  background-color: #00000060;
  height: 100%;
}
.banner img{
  width: 100vw;
}
.banner .title-container {
  display: grid;
  padding: 50px;
  align-items: center;
  font-size: 30px;
  text-shadow: 2px 2px #000000;
  justify-content: center;
  justify-items: center;
}

.banner .overview-container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  text-align: left;
  font-size: 21px;
  text-shadow: 1px 1px #000000;

}
.banner .overview-container .overview{
  grid-column: 2;
}
