.movie-slider-row {
}
.movie-slider-row img{
  height: auto;
}
.rows-container {
  width: 90vw;
  margin-left: 5vw ;
  margin-right: 5vw;
  color: white;
}
.banner-vote {
  margin-left: 30px;
  display: block;
}
a.trailer-btn {
  font-size: 20px;
  background-color: #ffffff70;
  width: 160px;
  padding: 5px;
  border-radius: 5px;
  text-align: center;
  line-height: 25px;
  text-decoration: none;
  color: #ffffff;
  margin-top: 10px;
}

a.trailer-btn:hover {
  font-weight: 600;
}
.footer {
  height: 200px;
}
.row-container{
  margin-top: 30px;
}
.row-title { 
  padding: 15px;
  text-align: left;
  font-size: 30px;
}
.row-container .info-popup {
  display: flex;
  /* grid-template-columns: 1fr 1fr; */
  flex-direction: row;
  align-items: center;
  background-color: #131d2a;
  margin: 15px;
  border-radius: 10px;
}

@media screen and ( max-width: 1300px)  {
  .banner-info {
    grid-template-columns: 1fr;
  }
  .banner-info .overview-container{
    display: none;
  }
}

@media screen and ( max-width: 900px)  {
  .banner {
    height: 100%;
  }
}
@media screen and ( max-width: 600px)  {
  .banner .title-container {
    font-size: 20px;
  }
}
@media screen and ( max-width: 370px)  {
  .banner .title-container {
    font-size: 15px;
  }
}